import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Button,
  Checkbox,
  Dimmer,
  Grid,
  Header,
  Icon,
  Image,
  Loader,
  Segment,
  Transition,
} from "semantic-ui-react";
import { SET_MODAL_CONTENT } from "../../redux/settingReducer";
import { countryOptions } from "../../settings/countries";
import { formatDate, formatTime } from "../../settings/format";
import { KYC_STEP_05_IMAGE } from "../../settings/links";
import { post } from "../../utils/api";

function Tip() {
  const { setting } = useSelector((state) => state);
  const { library } = setting;
  const {
    VERIFY_NOTE,
    KYC_CONFIRM_NOTE,
    KYC_CONFIRM_NOTE_1,
    KYC_CONFIRM_NOTE_2,
    WELL_DONE,
  } = library;
  return (
    <>
      <div
        style={{
          backgroundColor: "var(--white)",
          width: "fit-content",
          margin: "auto",
          borderRadius: ".28571429rem",
        }}
      >
        <Image
          src={KYC_STEP_05_IMAGE}
          centered
          size="medium"
          style={{ borderRadius: ".28571429rem" }}
        />
      </div>
      <Header textAlign="center">{WELL_DONE}</Header>
      <div>- {KYC_CONFIRM_NOTE}</div>
      <div>- {KYC_CONFIRM_NOTE_1}</div>
      <div>- {KYC_CONFIRM_NOTE_2}</div>
      <div>{VERIFY_NOTE}</div>
    </>
  );
}

function KYCConfirm({ _previous }) {
  const { user } = useSelector((state) => state);
  const { verification, verificationPhotos } = user;
  const [checked, setChecked] = useState(false);
  const dispatch = useDispatch();
  // const tempDob = new Date(verification.dob).toLocaleDateString();
  const tempCountry =
    countryOptions[
      countryOptions.findIndex((e) => e.value === verification.country)
    ].text;
  const [loading, setLoading] = useState(false);

  const { front, back, selfie } = verificationPhotos;
  const dob = verification.dob.split("-");
  const tempDob = `${dob[0]}-${dob[1]}-${dob[2]}T00:00:00`;
  var requestSent = false;
  const _handleVerify = () => {
    if (checked && !requestSent) {
      requestSent = true;
      setLoading(true);
      post(
        `/user-service/user/identity-verification`,
        {
          FirstName: verification.firstName,
          MiddleName: verification.middleName,
          LastName: verification.lastName,
          DateOfBirth: tempDob,
          Nationality: tempCountry,
          ResidentialAddress: verification.address,
          PostalCode: verification.postalCode,
          City: verification.city,
          BackPhoto: verification.backPhoto,
          FrontPhoto: verification.frontPhoto,
          SelfiePhoto: verification.selfiePhoto,
          idCode: verification.idCode,
          IdType: verification.idType,
        },
        () => {
          dispatch({
            type: SET_MODAL_CONTENT,
            payload: {
              content: "KYC_SUCCESS",
              callback: () => {
                // setLoading(false);
                window.location.replace("/me");
              },
            },
          });
        },
        (error) => {
          alert(JSON.stringify(error));
        }
      );
    } else {
      dispatch({
        type: SET_MODAL_CONTENT,
        payload: {
          content: "PLEASER_CHECK_TOS",
          callback: () => {},
        },
      });
    }
  };

  const { setting } = useSelector((state) => state);
  const { library } = setting;
  const {
    FULL_NAME,
    DOB,
    ADDRESS,
    CITY,
    COUNTRY,
    FRONT_PAGE,
    BACK_PAGE,
    SELFIE,
    ID_CODE,
    CONFIRM,
    VERIFY_NOTE,
    WELL_DONE,
    KYC_CONFIRM_NOTE,
    KYC_CONFIRM_NOTE_1,
    KYC_CONFIRM_NOTE_2,
    KYC_CONFIRM_NOTE_3,
    TERM_OF_USE,
    AND,
    AGREE_AFTER,
    I_AGREE_TO_THE,
  } = library;

  return (
    <>
      <Dimmer page active={loading}>
        <Loader />
      </Dimmer>
      <Segment basic vertical textAlign="left">
        <Icon
          name="arrow left"
          onClick={_previous}
          size="large"
          style={{ cursor: "pointer" }}
        />
      </Segment>
      <Grid textAlign="left">
        <Grid.Column computer={8} tablet={8} mobile={16}>
          <div
            style={{
              backgroundColor: "var(--white)",
              width: "fit-content",
              margin: "auto",
              borderRadius: ".28571429rem",
            }}
          >
            <Image
              src={KYC_STEP_05_IMAGE}
              centered
              size="medium"
              style={{ borderRadius: ".28571429rem" }}
            />
          </div>
          <Header textAlign="center">{WELL_DONE}</Header>
          <Checkbox
            label={() => (
              <label style={{ color: "#fff" }}>
                {I_AGREE_TO_THE}{" "}
                <a href="/terms-of-service" target="_blank">
                  {TERM_OF_USE}
                </a>{" "}
                , <span>{AGREE_AFTER}</span> {library.AND}{" "}
                <span>{library.SPEXCHANGE_KYC_NOTE}</span>
              </label>
            )}
            checked={checked}
            onChange={(e, { checked }) => setChecked(checked)}
            className="hide-mobile"
          />
          <Segment vertical className="hide-mobile">
            <Button onClick={_handleVerify} className="spwallet-button" fluid>
              {CONFIRM}
            </Button>
          </Segment>
          <Header as="h5">{KYC_CONFIRM_NOTE_3}</Header>
          <div>- {KYC_CONFIRM_NOTE}</div>
          <div>- {KYC_CONFIRM_NOTE_1}</div>
          <div>- {KYC_CONFIRM_NOTE_2}</div>
          <br />
          <div>{VERIFY_NOTE}</div>
        </Grid.Column>
        <Grid.Column computer={8} tablet={8} mobile={16}>
          {/* <Icon
            name="lightbulb"
            onClick={() => setActive(!active)}
            size="large"
            className="show-mobile"
            style={{
              cursor: "pointer",
              position: "absolute",
              top: "-1em",
              right: "1em",
            }}
          />
          <Transition visible={active} animation="fade down" duration={500}>
            <Segment textAlign="left" basic>
              <Tip />
            </Segment>
          </Transition> */}
          <Segment vertical textAlign="left" style={{ paddingTop: 0 }}>
            <p>
              {FULL_NAME}: {verification.firstName} {verification.middleName}{" "}
              {verification.lastName}
            </p>
            <p>
              {ID_CODE}: {verification.idCode}
            </p>
            <p>
              {DOB}: {verification.dob}
            </p>
            <p>
              {ADDRESS}: {verification.address}
            </p>
            <p>
              {CITY}: {verification.city}
            </p>
            <p>
              {COUNTRY}: {tempCountry}
            </p>
            <Grid columns={2}>
              <Grid.Column>
                <p>{FRONT_PAGE}</p>
                <Image
                  src={front}
                  style={{
                    width: "100%",
                    objectFit: "cover",
                    height: 150,
                    border: "1px solid #fff",
                  }}
                />
              </Grid.Column>
              <Grid.Column>
                <p>{BACK_PAGE}</p>
                <Image
                  src={back}
                  style={{
                    width: "100%",
                    objectFit: "cover",
                    height: 150,
                    border: "1px solid #fff",
                  }}
                />
              </Grid.Column>
            </Grid>
            <Segment textAlign="center" basic>
              <p>{SELFIE}</p>
              <Image
                src={selfie}
                style={{
                  width: 200,
                  objectFit: "cover",
                  height: 200,
                  borderRadius: "50%",
                  border: "1px solid #fff",
                }}
                centered
              />
            </Segment>
          </Segment>
          <div className="show-mobile">
            <Checkbox
              label={() => (
                <label style={{ color: "#fff" }}>
                  <p>
                    {I_AGREE_TO_THE}{" "}
                    <a href="/terms-of-service" target="_blank">
                      {TERM_OF_USE}
                    </a>{" "}
                    {AND} <a>{AGREE_AFTER}.</a>
                  </p>
                </label>
              )}
              checked={checked}
              onChange={(e, { checked }) => setChecked(checked)}
            />
            <p></p>
            <Button onClick={_handleVerify} className="spwallet-button" fluid>
              {CONFIRM}
            </Button>
          </div>
        </Grid.Column>
      </Grid>
    </>
  );
}

export default KYCConfirm;
