import React from "react";
import { useSelector } from "react-redux";
import { Container, Header, Icon, Loader, Segment } from "semantic-ui-react";
import { formatTime } from "../../settings/format";

function Info() {
  const { user, setting } = useSelector((state) => state);
  const { data, activity } = user;
  const { library } = setting;
  const { LAST_LOGIN, PROFILE } = library;

  const lastActivity = activity && activity.items[0];

  return (
    data &&
    lastActivity && (
      <Segment className="information">
        <div>
          <span style={{ marginRight: "1em" }}>Email: {data.email}</span>
          <span style={{ marginRight: "1em" }}>ID:#{data.id}</span>
          <span style={{ color: "#fbbd08" }}>
            <Icon name="gem outline" />
            VIP{data.level}
          </span>
        </div>
        <div className="last-login">
          <span>
            {LAST_LOGIN}: {formatTime(lastActivity.time)}
          </span>
          <span className="ip"> IP:{lastActivity.ip}</span>
        </div>
      </Segment>
    )
  );
}

export default Info;
