import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Container, List, Loader, Segment } from "semantic-ui-react";
import { _getLoginActivity } from "../../redux/userReducer";
import { formatTime } from "../../settings/format";
import CustomLineLoader from "../CustomLineLoader";

function LoginActivity() {
  const { user } = useSelector((state) => state);
  const { activity } = user;

  // const dispatch = useDispatch();

  // useEffect(() => {
  //   dispatch(_getLoginActivity());
  // }, [dispatch]);

  return (
    activity && (
      <Segment className="login-list" basic vertical>
        <List divided verticalAlign="middle" inverted>
          {activity.items.map((d, index) => {
            return (
              <List.Item key={index}>
                <List.Content floated="right">
                  <List.Header as="div">
                    <Container textAlign="right" fluid>
                      {d.ip}
                    </Container>
                  </List.Header>
                  <List.Description as="div">
                    <Container textAlign="right" fluid>
                      {formatTime(d.time)}
                    </Container>
                  </List.Description>
                </List.Content>
                <List.Content>
                  <List.Header as="p">{d.device}</List.Header>
                  <List.Description as="p">{d.location}</List.Description>
                </List.Content>
              </List.Item>
            );
          })}
        </List>
      </Segment>
    )
  );
}

export default LoginActivity;
