import { Container } from "@material-ui/core";
import React from "react";
import LazyLoad from "react-lazyload";
import EasyBuyHistory from "../../components/easybuy/EasyBuyHistory";

function PreSaleHistory() {
  return (
    <LazyLoad>
      <Container>
        <EasyBuyHistory />
      </Container>
    </LazyLoad>
  );
}

export default PreSaleHistory;
