import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Container, List, Loader, Segment } from "semantic-ui-react";
import { _getDeviceList, _getLoginActivity } from "../../redux/userReducer";
import { formatTime } from "../../settings/format";

function DeviceList() {
  const { user } = useSelector((state) => state);
  const { device } = user;
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(_getDeviceList());
    dispatch(_getLoginActivity());
  }, [dispatch]);

  return (
    device && (
      <Segment className="device-list" basic vertical>
        <List divided verticalAlign="middle" inverted>
          {device.map((d, index) => {
            return (
              <List.Item key={index}>
                <List.Content floated="right">
                  <List.Header as="div">
                    <Container textAlign="right" fluid>
                      {d.ip}
                    </Container>
                  </List.Header>
                  <List.Description as="div">
                    <Container textAlign="right" fluid>
                      {formatTime(d.time)}
                    </Container>
                  </List.Description>
                </List.Content>
                <List.Content>
                  <List.Header as="p">{d.name}</List.Header>
                  <List.Description as="p">{d.location}</List.Description>
                </List.Content>
              </List.Item>
            );
          })}
        </List>
      </Segment>
    )
  );
}

export default DeviceList;
