import React, { useState } from "react";
import "./style.css";
import "./blink.css";
import { useDispatch, useSelector } from "react-redux";
import { post } from "../../utils/api";
import { Header, Icon, Modal, Segment, Button } from "semantic-ui-react";
import { _getGift } from "../../redux/userReducer";

function Index() {
  const { user, setting } = useSelector((state) => state);
  const { gift } = user;
  const { library } = setting;
  const { WISH_YOU_LUCK, CONGRATULATION, PRIZE_VALUE } = library;
  const [data, setData] = useState(null);
  const [show, setShow] = useState(false);

  const dispatch = useDispatch();

  const _handleOpenGift = () => {
    post(
      `/staking-service/gift/open/${gift.id}`,
      {},
      (data) => {
        setShow(true);
        setData(data);
        dispatch(_getGift());
      },
      (error) => console.error(error)
    );
  };

  if (gift) {
    return (
      <>
        {gift.isAvailable && (
          <div style={{ position: "fixed", right: "0.5em", bottom: "0.5em" }}>
            <div
              className="circle custom-pulse-button"
              onClick={_handleOpenGift}
            >
              <i className="gift">
                <i className="ribbons"></i>
              </i>
            </div>
          </div>
        )}
        {data && (
          <Modal open={show} size="mini">
            {data.status === "PENDING" ? (
              <Modal.Header>
                <Segment textAlign="center" basic>
                  <Icon name="star" size="massive" color="yellow" />
                  <Header as="h2">{WISH_YOU_LUCK}</Header>
                  <Button
                    className="spwallet-button"
                    onClick={() => setShow(false)}
                  >
                    OK
                  </Button>
                </Segment>
              </Modal.Header>
            ) : (
              <Modal.Header>
                <Segment textAlign="center" basic>
                  <Icon name="trophy" size="massive" color="yellow" />
                  <Header as="h2">{CONGRATULATION}</Header>
                  <Header>
                    {PRIZE_VALUE}: {data.amount} mUSD
                  </Header>
                  <Button
                    className="spwallet-button"
                    onClick={() => setShow(false)}
                  >
                    OK
                  </Button>
                </Segment>
              </Modal.Header>
            )}
          </Modal>
        )}
      </>
    );
  } else return null;
}

export default Index;
