import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useParams } from "react-router-dom";
import {
  Button,
  Confirm,
  Header,
  Icon,
  Pagination,
  Popup,
  Segment,
  Tab,
  Table,
} from "semantic-ui-react";
import { SET_MODAL_CONTENT } from "../../redux/settingReducer";
import { _getFundList, _getWithdrawHistory } from "../../redux/walletReducer";
import { _delete } from "../../utils/api";
import { formatAddress, formatAmount, formatTime } from "../../settings/format";
import { format } from "crypto-js";
import { getLinkHash } from "../../settings";

function WithdrawHistory() {
  const { wallet, setting } = useSelector((state) => state);
  const { library } = setting;
  const { withdrawHistory } = wallet;
  const dispatch = useDispatch();
  const { coin } = useParams();
  const [activePage, setActivePage] = useState(1);
  const [openConfirm, setOpenConfirm] = useState(false);
  const [id, setId] = useState(null);

  useEffect(() => {
    dispatch(_getWithdrawHistory(coin, 0, 0, 1, 10));
  }, [coin, dispatch]);

  const _handleCancel = () => {
    setOpenConfirm(false);
    _delete(`/fund-service/withdraw/${id}`, {}, (data) => {
      dispatch({
        type: SET_MODAL_CONTENT,
        payload: {
          content: "CANCEL_WITHDRAW",
          callback: () => {
            dispatch(_getWithdrawHistory(coin, 0, 0, activePage, 10));
            dispatch(_getFundList());
          },
        },
      });
    });
  };

  const {
    TIME,
    TYPE,
    ADDRESS,
    AMOUNT,
    STATUS,
    CANCEL,
    NO_RECORDS_FOUND,
    WITHDRAW_HISTORY,
  } = library;

  return (
    withdrawHistory && (
      <Segment textAlign="center">
        <Header textAlign="left">{WITHDRAW_HISTORY}</Header>
        <Segment className="spwallet-history" textAlign="center" vertical basic>
          <Table
            unstackable
            basic="very"
            compact="very"
            celled
            inverted
            singleLine
            selectable
            textAlign="left"
          >
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>ID</Table.HeaderCell>
                <Table.HeaderCell>{TIME}</Table.HeaderCell>
                <Table.HeaderCell>{TYPE}</Table.HeaderCell>
                <Table.HeaderCell>{ADDRESS}</Table.HeaderCell>
                <Table.HeaderCell>Hash</Table.HeaderCell>
                <Table.HeaderCell>{AMOUNT}</Table.HeaderCell>
                <Table.HeaderCell>{STATUS}</Table.HeaderCell>
                <Table.HeaderCell></Table.HeaderCell>
              </Table.Row>
            </Table.Header>
            <Table.Body>
              {withdrawHistory.items.length > 0 ? (
                withdrawHistory.items.map((h, index) => (
                  <Table.Row key={index}>
                    <Table.Cell>#{h.id}</Table.Cell>
                    <Table.Cell>{formatTime(h.time)}</Table.Cell>
                    <Table.Cell>{library[h.type]}</Table.Cell>
                    <Table.Cell>
                      {h.address && (
                        <Popup
                          content="Copied"
                          on="click"
                          basic
                          position="bottom center"
                          hideOnScroll
                          trigger={
                            <p
                              style={{
                                textOverflow: "ellipsis",
                                overflow: "hidden",
                                cursor: "pointer",
                              }}
                              onClick={() =>
                                navigator.clipboard.writeText(h.address)
                              }
                            >
                              {formatAddress(h.address)}
                              <Icon
                                name="copy outline"
                                style={{ fontSize: "1em" }}
                              />
                            </p>
                          }
                        />
                      )}
                    </Table.Cell>
                    <Table.Cell>{getLinkHash(h)}</Table.Cell>
                    <Table.Cell>
                      {formatAmount(h.amount)} {h.coin}
                    </Table.Cell>
                    <Table.Cell>{library[h.status]}</Table.Cell>
                    <Table.Cell>
                      {(h.status === "WAITING_CONFIRM" ||
                        h.status === "PENDING") && (
                        <Link
                          to="#"
                          onClick={() => {
                            setId(h.id);
                            setOpenConfirm(true);
                          }}
                        >
                          {CANCEL}
                        </Link>
                      )}
                    </Table.Cell>
                  </Table.Row>
                ))
              ) : (
                <Table.Row>
                  <Table.Cell colSpan={6}>
                    <Segment basic textAlign="center">
                      {NO_RECORDS_FOUND}
                    </Segment>
                  </Table.Cell>
                </Table.Row>
              )}
            </Table.Body>
          </Table>
        </Segment>
        {withdrawHistory.pageCount > 1 && (
          <Pagination
            totalPages={withdrawHistory.pageCount}
            activePage={withdrawHistory.page}
            onPageChange={(e, { activePage }) => {
              setActivePage(activePage);
              dispatch(_getWithdrawHistory(coin, 0, 0, activePage, 10));
            }}
            pointing
            secondary
            inverted
            ellipsisItem={null}
            firstItem={null}
            lastItem={null}
            siblingRange={1}
            boundaryRange={0}
          />
        )}
        <Confirm
          open={openConfirm}
          onCancel={() => setOpenConfirm(false)}
          onConfirm={_handleCancel}
          content={library.ARE_YOU_SURE}
          confirmButton={<Button className="spwallet-button">OK</Button>}
          cancelButton={library.CANCEL}
          size="mini"
          className="spwallet-animation"
        />
      </Segment>
    )
  );
}

export default WithdrawHistory;
