import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Header, Pagination, Segment, Table } from "semantic-ui-react";
import { _getEasyBuyHistory } from "../../redux/walletReducer";
import { formatAmount, formatTime } from "../../settings/format";

function EasyBuyHistory() {
  const { wallet, setting } = useSelector((state) => state);
  const { easyBuyHistory } = wallet;
  const dispatch = useDispatch();
  const { library } = setting;
  const {
    TIME,
    PRICE,
    BASE_AMOUNT,
    QUOTE_AMOUNT,
    NO_RECORDS_FOUND,
    SWAP_HISTORY,
  } = library;
  useEffect(() => {
    dispatch(_getEasyBuyHistory());
  }, [dispatch]);

  return (
    easyBuyHistory && (
      <Segment textAlign="center">
        <Header textAlign="left">{library.HISTORY}</Header>
        <Segment className="spwallet-history" vertical basic>
          <Table
            unstackable
            basic="very"
            compact="very"
            celled
            inverted
            singleLine
            selectable
            textAlign="left"
          >
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>ID</Table.HeaderCell>
                <Table.HeaderCell>{TIME}</Table.HeaderCell>
                <Table.HeaderCell className="hide-mobile">
                  {PRICE}
                </Table.HeaderCell>
                <Table.HeaderCell>{library.BUY}</Table.HeaderCell>
                <Table.HeaderCell>{library.TOTAL}</Table.HeaderCell>
              </Table.Row>
            </Table.Header>
            <Table.Body>
              {easyBuyHistory.items.length > 0 ? (
                easyBuyHistory.items.map((s, index) => (
                  <Table.Row key={index}>
                    <Table.Cell>#{s.id}</Table.Cell>
                    <Table.Cell>{formatTime(s.time)}</Table.Cell>
                    <Table.Cell className="hide-mobile">
                      {formatAmount(s.price)} {s.quote} / {s.base}
                    </Table.Cell>
                    <Table.Cell>
                      {formatAmount(s.amount)} {s.base}
                    </Table.Cell>
                    <Table.Cell>
                      {formatAmount(s.total)} {s.quote}
                    </Table.Cell>
                  </Table.Row>
                ))
              ) : (
                <Table.Row>
                  <Table.Cell colSpan={5}>
                    <Segment basic textAlign="center">
                      {NO_RECORDS_FOUND}
                    </Segment>
                  </Table.Cell>
                </Table.Row>
              )}
            </Table.Body>
          </Table>
        </Segment>
        {easyBuyHistory.pageCount > 1 && (
          <Pagination
            totalPages={easyBuyHistory.pageCount}
            activePage={easyBuyHistory.page}
            onPageChange={(e, { activePage }) =>
              dispatch(_getEasyBuyHistory(activePage))
            }
            pointing
            secondary
            inverted
            ellipsisItem={null}
            firstItem={null}
            lastItem={null}
            siblingRange={1}
            boundaryRange={0}
          />
        )}
      </Segment>
    )
  );
}

export default EasyBuyHistory;
