import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  List,
  Loader,
  Modal,
  Segment,
  Table,
  Transition,
} from "semantic-ui-react";
import { _getStakingProduct } from "../../redux/walletReducer";
import StakingItem from "./StakingItem";

function StakingList() {
  const { wallet, setting } = useSelector((state) => state);
  const { stakingProduct } = wallet;
  const dispatch = useDispatch();
  const [showStakingPolicy, setShowStakingPolicy] = useState(false);

  useEffect(() => dispatch(_getStakingProduct()), [dispatch]);

  const stakingList = [];

  stakingProduct &&
    stakingProduct.sort((a, b) => {
      return a.duration - b.duration;
    });

  stakingProduct &&
    stakingProduct.forEach((element) => {
      if (!stakingList.includes(element.coin)) {
        stakingList.push(element.coin);
      }
    });

  const { library } = setting;
  const {
    DURATION,
    DAYS,
    MINIMUM_AMOUNT,
    EST_APY,
    COIN_TOKEN,
    STAKING_POLICY_0,
    STAKING_POLICY_1,
    STAKING_POLICY_2,
    STAKING_POLICY_3,
    STAKING_POLICY_4,
    STAKING_POLICY_5,
    STAKING_POLICY_6,
    MINIMUM
  } = library;

  const tips = [
    STAKING_POLICY_0,
    STAKING_POLICY_1,
    STAKING_POLICY_2,
    STAKING_POLICY_3,
    STAKING_POLICY_4,
    STAKING_POLICY_5,
    STAKING_POLICY_6,
  ];

  return (
    stakingProduct && (
      <Segment>
        <Table basic="very" inverted compact="very" fixed unstackable>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell>{COIN_TOKEN}</Table.HeaderCell>
              <Table.HeaderCell>{EST_APY}</Table.HeaderCell>
              <Table.HeaderCell className="hide-mobile">
                {DURATION} ({DAYS})
              </Table.HeaderCell>
              <Table.HeaderCell className="hide-mobile">
                {MINIMUM}
              </Table.HeaderCell>
              <Table.HeaderCell></Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body>
            {stakingList.map((s, index) => (
              <StakingItem
                coin={s}
                key={index}
                showStakingPolicy={() => setShowStakingPolicy(true)}
              />
            ))}
          </Table.Body>
        </Table>
        <Transition
          visible={showStakingPolicy}
          animation="fade up"
          duration={500}
        >
          <Modal
            open={showStakingPolicy}
            size="small"
            onClose={() => setShowStakingPolicy(false)}
            closeIcon
            basic
          >
            <Modal.Header>{library.STAKING_POLICY}</Modal.Header>
            <Modal.Content>
              <List.Item as="ul">
                {tips.map((item, index) => (
                  <List.Item key={index} as="li">
                    {item}
                  </List.Item>
                ))}
              </List.Item>
            </Modal.Content>
          </Modal>
        </Transition>
      </Segment>
    )
  );
}

export default StakingList;
