import React, { useEffect, useState } from "react";
import LazyLoad from "react-lazyload";
import { useSelector } from "react-redux";
import { Link, useHistory, useParams } from "react-router-dom";
import {
  Container,
  Dropdown,
  Grid,
  Header,
  Icon,
  Image,
  Label,
  List,
  Segment,
  Tab,
} from "semantic-ui-react";
import DepositForm from "../../components/deposit/DepositForm";
import DepositHistory from "../../components/deposit/DepositHistory";
import { S3_URL } from "../../settings";
import { formatAmount, formatMoney, formatTotal } from "../../settings/format";

function Deposit() {
  const { coin } = useParams();
  const { wallet, api, user, setting } = useSelector((state) => state);
  const { fundList } = wallet;
  const fund = fundList.find((f) => f.coin === coin);
  const [panes, setPanes] = useState(null);
  const [oldCoin, setOldCoin] = useState(null);
  const history = useHistory();

  useEffect(() => {
    if (fund) {
      const temp = [];
      fund.networks.forEach((element) => {
        temp.push({
          menuItem: element.network,
          render: () => <DepositForm coin={coin} network={element.network} />,
        });
      });
      if (coin !== oldCoin) {
        setPanes(temp);
        setOldCoin(coin);
      }
    }
  }, [fund, panes, coin, api, user.accessToken, oldCoin, setOldCoin]);

  const { library } = setting;
  const {
    VALUE,
    GO_TO_DEPOSIT,
    DEPOSIT_TIP_0_BEGIN,
    DEPOSIT_TIP_0_END,
    DEPOSIT_TIP_1,
    DEPOSIT_TIP_2,
    DEPOSIT_TIP_3,
    DEPOSIT_TIP_4,
  } = library;

  const tips = [
    DEPOSIT_TIP_0_BEGIN + " " + coin + " " + DEPOSIT_TIP_0_END,
    DEPOSIT_TIP_1,
    DEPOSIT_TIP_2,
    DEPOSIT_TIP_3,
    DEPOSIT_TIP_4,
  ];

  const selectFund = [];
  if (fundList) {
    fundList.forEach((element, index) => {
      selectFund.push({
        key: element.coin,
        value: element.coin,
        text: element.coin + " - " + element.amount,
        image: { avatar: true, src: `${S3_URL}/coins/${element.icon}` },
      });
    });
  }

  return fund ? (
    <Container textAlign="left" className="spwallet-deposit spwallet-container">
      <LazyLoad>
        <Header as="h1">{library.DEPOSIT}</Header>
        <Grid stretched>
          <Grid.Column computer={8} tablet={8} only="computer">
            <Segment>
              {selectFund.length > 0 && (
                <Dropdown
                  options={selectFund}
                  selection
                  fluid
                  onChange={(e, { value }) => history.push(`/deposit/${value}`)}
                  value={history.location.pathname.replace("/deposit/", "")}
                />
              )}
              <Segment vertical basic textAlign="right">
                <Label as="h5">
                  {VALUE}: ~ ${formatTotal(fund.usdAmount)}
                </Label>
              </Segment>
              <Segment className="spwallet-tips">
                <Segment vertical basic>
                  <Icon name="lightbulb" size="large" />
                  Tips:
                </Segment>
                <List.Item as="ul">
                  {tips.map((item, index) => (
                    <List.Item key={index} as="li">
                      {item}
                    </List.Item>
                  ))}
                </List.Item>
              </Segment>
              {/* <Segment vertical>
              {GO_TO_DEPOSIT}:{" "}
              {fundList.map((item, index) => (
                <Link
                  key={index}
                  to={`/deposit/${item.coin}`}
                  style={{
                    paddingRight: "0.5em",
                    textDecoration: "underline",
                  }}
                >
                  {item.coin}
                </Link>
              ))}
            </Segment> */}
            </Segment>
          </Grid.Column>
          <Grid.Column computer={8} tablet={8} mobile={16}>
            <Segment textAlign="center" className="spwallet-deposit_form">
              {/* <Image
              src={`/images/${coin.toLowerCase()}.png`}
              size="mini"
              centered
            /> */}

              <div className="show-mobile">
                {selectFund.length > 0 && (
                  <Dropdown
                    options={selectFund}
                    selection
                    fluid
                    onChange={(e, { value }) =>
                      history.push(`/deposit/${value}`)
                    }
                    value={history.location.pathname.replace("/deposit/", "")}
                  />
                )}
                <Segment vertical basic textAlign="right">
                  <Label as="h5">
                    {VALUE}: ~ ${formatTotal(fund.usdAmount)}
                  </Label>
                </Segment>
              </div>
              <Tab
                panes={panes}
                menu={{ secondary: true, pointing: true, inverted: true }}
              />
            </Segment>
          </Grid.Column>
        </Grid>
        <DepositHistory coin={coin} />
      </LazyLoad>
    </Container>
  ) : null;
}

export default Deposit;
