import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { Header, Pagination, Segment, Table } from "semantic-ui-react";
import { formatTime } from "../../settings/format";
import { get, post } from "../../utils/api";

function ListRedeems() {
  const [redeems, setRedeems] = useState(null);
  const { setting } = useSelector((state) => state);
  const { library } = setting;
  const { TIME, AMOUNT, NO_RECORDS_FOUND } = library;
  useEffect(() => {
    !redeems &&
      post(
        `/staking-service/staking/list-redeems`,
        {
          page: 1,
          pageSize: 10,
        },
        (data) => {
          setRedeems(data);
        },
        (error) => console.error(error)
      );
  });

  return (
    redeems && (
      <Segment basic vertical>
        <Segment basic vertical className="spwallet-history">
          <Table
            unstackable
            basic="very"
            compact="very"
            celled
            inverted
            singleLine
          >
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>{TIME}</Table.HeaderCell>
                <Table.HeaderCell>{AMOUNT}</Table.HeaderCell>
              </Table.Row>
            </Table.Header>
            <Table.Body>
              {redeems.items.length > 0 ? (
                redeems.items.map((item, index) => (
                  <Table.Row key={index}>
                    <Table.Cell>{formatTime(item.time)}</Table.Cell>
                    <Table.Cell>
                      {item.amount} {item.coin}
                    </Table.Cell>
                  </Table.Row>
                ))
              ) : (
                <Table.Row>
                  <Table.Cell colSpan={6}>
                    <Segment basic textAlign="center">
                      {NO_RECORDS_FOUND}
                    </Segment>
                  </Table.Cell>
                </Table.Row>
              )}
            </Table.Body>
          </Table>
        </Segment>
        {redeems.pageCount > 1 && (
          <Segment vertical textAlign="center">
            <Pagination
              activePage={redeems.page}
              totalPages={redeems.pageCount}
              onPageChange={(e, { activePage }) =>
                post(
                  `/staking-service/staking/list-redeems`,
                  {
                    page: activePage,
                    pageSize: 10,
                  },
                  (data) => {
                    setRedeems(data);
                  },
                  (error) => console.error(error)
                )
              }
              pointing
              secondary
              inverted
              ellipsisItem={null}
              firstItem={null}
              lastItem={null}
              siblingRange={1}
              boundaryRange={0}
            />
          </Segment>
        )}
      </Segment>
    )
  );
}

export default ListRedeems;
