import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Pagination, Segment, Table } from "semantic-ui-react";
import { _getGiftRewards } from "../../redux/userReducer";
import { formatTime } from "../../settings/format";

function Gift() {
  const { user, setting } = useSelector((state) => state);
  const { giftRewards } = user;
  const { library } = setting;
  const dispatch = useDispatch();
  const [page, setPage] = useState(1);
  const { TIME, AMOUNT, TYPE, NO_RECORDS_FOUND } = library;

  useEffect(() => {
    dispatch(_getGiftRewards(page));
  }, [dispatch, page]);

  return (
    giftRewards && (
      <>
        <Segment className="spwallet-history" basic vertical>
          <Table
            unstackable
            basic="very"
            compact="very"
            celled
            inverted
            singleLine
            selectable
          >
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>{TIME}</Table.HeaderCell>
                <Table.HeaderCell>{AMOUNT}</Table.HeaderCell>
              </Table.Row>
            </Table.Header>
            <Table.Body>
              {giftRewards.items.length > 0 ? (
                giftRewards.items.map((item, index) => (
                  <Table.Row key={index}>
                    <Table.Cell>{formatTime(item.time)}</Table.Cell>
                    <Table.Cell>
                      {item.amount} {item.coin}
                    </Table.Cell>
                  </Table.Row>
                ))
              ) : (
                <Table.Row>
                  <Table.Cell colSpan={3}>
                    <Segment basic textAlign="center">
                      {NO_RECORDS_FOUND}
                    </Segment>
                  </Table.Cell>
                </Table.Row>
              )}
            </Table.Body>
          </Table>
        </Segment>
        {giftRewards.pageCount > 1 && (
          <Pagination
            totalPages={giftRewards.pageCount}
            activePage={giftRewards.page}
            pointing
            secondary
            inverted
            onPageChange={(e, { activePage }) => setPage(activePage)}
            ellipsisItem={null}
            firstItem={null}
            lastItem={null}
            siblingRange={1}
            boundaryRange={0}
          />
        )}
      </>
    )
  );
}

export default Gift;
