import React, { useState } from "react";
import LazyLoad from "react-lazyload";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import {
  Button,
  Checkbox,
  Confirm,
  Dimmer,
  Grid,
  Header,
  Icon,
  Image,
  Input,
  Loader,
  Message,
  Modal,
  Segment,
  Table,
  Transition,
} from "semantic-ui-react";
import { SET_MODAL_CONTENT } from "../../redux/settingReducer";
import { _getGift } from "../../redux/userReducer";
import { _getStakingHistory, _getFundList } from "../../redux/walletReducer";
import { S3_URL } from "../../settings";
import format, {
  formatAmountInput,
  formatAmount,
  deleteText,
  formatDate,
  formatTime,
  formatStakingTime,
} from "../../settings/format";
import { post } from "../../utils/api";
import AmountInput from "../AmountInput";

function StakingItem({ coin, showStakingPolicy }) {
  const { wallet, setting } = useSelector((state) => state);
  const [option, setOption] = useState(0);
  const [open, setOpen] = useState(false);
  const { stakingProduct, fundList } = wallet;
  const lockedStackingList = [];
  const dispatch = useDispatch();
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);
  const [amount, setAmount] = useState("");
  const { library } = setting;
  const fund = fundList.find((f) => f.coin === coin);
  const [openConfirm, setOpenConfirm] = useState(false);
  const [policyCheck, setPolicyCheck] = useState(false);

  stakingProduct.forEach((element) => {
    if (element.coin === coin) lockedStackingList.push(element);
  });

  const _handleSelectDuration = (value) => {
    setOption(value);
  };

  const _handleClose = (e) => {
    if (e.target.className === "close icon" && !loading) {
      setAmount("");
      setError(null);
      setLoading(false);
      setOpen(false);
    }
  };

  const _checkStaking = () => {
    setError(null);
    const tempAmount = parseFloat(formatAmountInput(amount));
    if (amount <= 0) {
      setError(library.PLEASE_ENTER_AMOUNT_FOR_STAKING);
    } else if (tempAmount < currentProduct.minAmount) {
      setError(
        library.PLEASE_ENTER_AMOUNT_MORE_THAN +
          " " +
          currentProduct.minAmount +
          " " +
          coin
      );
      setLoading(false);
    } else if (tempAmount > fund.amount) {
      setError(library.UNAVAILABLE_BALANCE);
    } else if (!policyCheck) {
      setError(library.PLEASER_CHECK_STAKING_POLICY);
    } else {
      setOpenConfirm(true);
      setOpen(false);
    }
  };

  const _handleStaking = () => {
    const tempAmount = parseFloat(formatAmountInput(amount));
    setOpenConfirm(false);
    setLoading(true);
    post(
      "/staking-service/staking",
      {
        productId: currentProduct.id,
        coin,
        amount: tempAmount,
      },
      () => {
        dispatch({
          type: SET_MODAL_CONTENT,
          payload: {
            content: "CREATE_STAKING_SUCCESS",
            callback: () => {
              setOpen(false);
              setLoading(false);
              setAmount("");
              dispatch(_getStakingHistory());
              dispatch(_getFundList());
              dispatch(_getGift());
            },
          },
        });
      },
      (error) => {
        setLoading(false);
        setError(library[error.code]);
      }
    );
  };

  const currentProduct = lockedStackingList[option];

  const _handleChangeAmount = (e) => {
    setAmount(deleteText(e.target.value));
  };

  const {
    STAKE,
    LOCK,
    MINIMUM_LOCKED_AMOUNT,
    MAXIMUM_LOCKED_AMOUNT,
    REDEMPTION_PERIOD,
    DURATION,
    LOCK_AMOUNT,
    CONFIRM_PURCHASE,
    PLEASE_ENTER_AMOUNT,
    PLEASE_ENTER_AMOUNT_FOR_STAKING,
    DAYS,
    EST_APY,
    STAKING,
  } = library;

  return (
    <>
      <Table.Row verticalAlign="middle">
        <Table.Cell>
          <Image src={`${S3_URL}/coins/${coin}.png`} size="mini" inline />{" "}
          {coin}
        </Table.Cell>
        <Table.Cell style={{ fontWeight: 600 }}>
          {currentProduct.estimateApy}%
        </Table.Cell>
        <Table.Cell className="hide-mobile">
          <div className="spwallet-staking-option">
            {lockedStackingList.map((l, index) => (
              <div
                key={index}
                className={option === index ? "active" : ""}
                onClick={() => _handleSelectDuration(index)}
              >
                {l.duration === 0 ? "Flexible Lock" : l.duration}
              </div>
            ))}
          </div>
        </Table.Cell>
        <Table.Cell className="hide-mobile">
          {currentProduct.minAmount} {coin}
        </Table.Cell>
        <Table.Cell>
          <Button className="spwallet-button" onClick={() => setOpen(true)}>
            {STAKE}
          </Button>
        </Table.Cell>
      </Table.Row>
      <Transition visible={open} animation="fade up" duration={500}>
        <Modal
          onClose={_handleClose}
          onOpen={() => setOpen(true)}
          open={open}
          // trigger={<Button className="spwallet-button">{STAKE}</Button>}
          closeIcon
          basic
        >
          <Modal.Header>
            {STAKING} {coin}
          </Modal.Header>
          <Modal.Content>
            <Segment className="spwallet-staking-tips">
              <Segment vertical basic>
                <Icon name="lightbulb" size="large" />
                Tips:
              </Segment>
              <Segment basic vertical>
                <p>{library.STAKING_TIP_0}</p>
                <p>{library.STAKING_TIP_1}</p>
              </Segment>
            </Segment>
            <Grid>
              <Grid.Column computer={8} tablet={8} mobile={16}>
                <Segment basic vertical>
                  <Grid columns={2}>
                    <Grid.Column> {library.TYPE}: </Grid.Column>
                    <Grid.Column textAlign="right">
                      {library[currentProduct.type]}
                    </Grid.Column>
                  </Grid>
                  <Grid columns={2}>
                    <Grid.Column> {library.ESTIMATED_BALANCE}: </Grid.Column>
                    <Grid.Column textAlign="right">
                      {fund && formatAmount(fund.amount)} {coin}
                    </Grid.Column>
                  </Grid>
                  <Grid columns={2}>
                    <Grid.Column> {library.MINIMUM}: </Grid.Column>
                    <Grid.Column textAlign="right">
                      {currentProduct.minAmount} {coin}
                    </Grid.Column>
                  </Grid>
                  {/* <Grid columns={2}>
                    <Grid.Column> {REDEMPTION_PERIOD}:</Grid.Column>
                    <Grid.Column textAlign="right">
                      {currentProduct.redeemPeriod} {DAYS}
                    </Grid.Column>
                  </Grid> */}
                  <Grid columns={2}>
                    <Grid.Column>{library.ESTIMATED_APY}: </Grid.Column>
                    <Grid.Column textAlign="right">
                      {currentProduct.estimateApy}%
                    </Grid.Column>
                  </Grid>
                </Segment>
              </Grid.Column>
              <Grid.Column computer={8} tablet={8} mobile={16}>
                <Segment vertical basic>
                  <label>
                    {library.STAKING_TIME} ({DAYS.toLowerCase()})
                  </label>
                  <div
                    className="spwallet-staking-option"
                    style={{ marginTop: "1em" }}
                  >
                    {lockedStackingList.map((l, index) => (
                      <div
                        key={index}
                        className={option === index ? "active" : ""}
                        onClick={() => _handleSelectDuration(index)}
                      >
                        {l.duration === 0 ? "Flexible Lock" : l.duration}
                      </div>
                    ))}
                  </div>
                </Segment>
                <Segment vertical basic>
                  <label>{LOCK_AMOUNT}</label>
                  <AmountInput
                    fluid
                    placeholder={PLEASE_ENTER_AMOUNT_FOR_STAKING}
                    onChange={_handleChangeAmount}
                    style={{ marginTop: "1em" }}
                    value={amount}
                  />
                </Segment>
                <div style={{ display: "flex" }}>
                  <Checkbox
                    checked={policyCheck}
                    onChange={(e, { checked }) => setPolicyCheck(checked)}
                  />
                  <div style={{ color: "#fff", marginLeft: 10 }}>
                    {library.AGREE_TO}{" "}
                    <a href="/terms-of-service" target="_blank">
                      {library.SPWALLET_TOS}
                    </a>{" "}
                    {library.AND}{" "}
                    <Link to="#" onClick={showStakingPolicy}>
                      {library.STAKING_POLICY}
                    </Link>
                    .
                  </div>
                </div>
                <Message
                  content={error}
                  positive
                  error
                  hidden={error === null}
                />
              </Grid.Column>
            </Grid>
          </Modal.Content>
          <Modal.Actions>
            <Button
              onClick={_checkStaking}
              className="spwallet-button"
              disabled={loading}
              loading={loading}
            >
              {STAKE}
            </Button>
          </Modal.Actions>
          {/* <Confirm
            open={openConfirm}
            onCancel={() => setOpenConfirm(false)}
            onConfirm={_handleStaking}
            content={library.ARE_YOU_SURE}
            confirmButton={<Button className="spwallet-button">OK</Button>}
            cancelButton={library.CANCEL}
            size="mini"
          /> */}
        </Modal>
      </Transition>
      <Dimmer page active={openConfirm}>
        {/* <LazyLoad> */}
        <Segment style={{ backgroundColor: "var(--light-blue)", width: 350 }}>
          <Segment vertical>
            <Header style={{ color: "var(--green)" }}>
              {library.STAKING_CONFIRM}
              <Icon
                name="close"
                style={{
                  position: "absolute",
                  right: 0,
                  top: 0,
                  cursor: "pointer",
                }}
                size="large"
                onClick={() => setOpenConfirm(false)}
              />
            </Header>
          </Segment>
          <Segment vertical>
            <Segment style={{ backgroundColor: "var(--gray)" }}>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                }}
              >
                <p>{library.LOCK_AMOUNT}:</p>
                <p>
                  {formatAmount(amount)} {coin}
                </p>
              </div>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                }}
              >
                <p>{library.STAKING_TIME}:</p>
                <p>
                  {currentProduct.duration} {library.DAYS.toLowerCase()}
                </p>
              </div>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                }}
              >
                <p>{library.STAKING_START_DATE}:</p>
                <p>{formatStakingTime(new Date())}</p>
              </div>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                }}
              >
                <p>{library.STAKING_END_DATE}:</p>
                <p>
                  {formatStakingTime(
                    new Date().getTime() +
                      currentProduct.duration * 24 * 60 * 60 * 1000
                  )}
                </p>
              </div>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                }}
              >
                <p>{library.ESTIMATED_APY}:</p>
                <p>{currentProduct.estimateApy}%</p>
              </div>
            </Segment>
            <Button className="spwallet-button" fluid onClick={_handleStaking}>
              {library.CONFIRM}
            </Button>
          </Segment>
        </Segment>
        {/* </LazyLoad> */}
      </Dimmer>
    </>
  );
}

export default StakingItem;
