import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { Header, Pagination, Segment, Table } from "semantic-ui-react";
import { SET_MODAL_CONTENT } from "../../redux/settingReducer";
import { _getFundList, _getStakingHistory } from "../../redux/walletReducer";
import { formatStakingTime, formatTime } from "../../settings/format";
import { _delete } from "../../utils/api";

function StakingHistory() {
  const { wallet, setting } = useSelector((state) => state);
  const { library } = setting;
  const { stakingHistory } = wallet;
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(_getStakingHistory());
  }, [dispatch]);

  const _handleCancel = (id) => {
    _delete(`/staking-service/staking/${id}`, {}, () => {
      dispatch({
        type: SET_MODAL_CONTENT,
        payload: {
          content: "CANCEL_STAKING_SUCCESS",
          callback: () => {
            dispatch(_getStakingHistory());
            dispatch(_getFundList());
          },
        },
      });
    });
  };

  const {
    TIME,
    TYPE,
    AMOUNT,
    DURATION,
    STATUS,
    CANCEL,
    DAYS,
    NO_RECORDS_FOUND,
    EST_APY,
  } = library;

  return (
    stakingHistory && (
      <Segment basic vertical textAlign="center">
        <Segment basic vertical className="spwallet-history">
          <Table
            unstackable
            basic="very"
            compact="very"
            celled
            inverted
            textAlign="left"
            singleLine
            selectable
          >
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>ID</Table.HeaderCell>
                <Table.HeaderCell>{library.START_TIME}</Table.HeaderCell>
                {/* <Table.HeaderCell>{TYPE}</Table.HeaderCell> */}
                <Table.HeaderCell>{AMOUNT}</Table.HeaderCell>
                <Table.HeaderCell>
                  {DURATION} ({DAYS})
                </Table.HeaderCell>
                <Table.HeaderCell>{library.END_TIME}</Table.HeaderCell>
                <Table.HeaderCell>{EST_APY}</Table.HeaderCell>
                <Table.HeaderCell>{STATUS}</Table.HeaderCell>
                {/* <Table.HeaderCell></Table.HeaderCell> */}
              </Table.Row>
            </Table.Header>
            <Table.Body>
              {stakingHistory.items.length > 0 ? (
                stakingHistory.items.map((s, index) => (
                  <Table.Row key={index}>
                    <Table.Cell>#{s.id}</Table.Cell>
                    <Table.Cell>{formatTime(s.time)}</Table.Cell>
                    {/* <Table.Cell>{s.type}</Table.Cell> */}
                    <Table.Cell>
                      {s.amount} {s.coin}
                    </Table.Cell>
                    <Table.Cell>{s.duration}</Table.Cell>
                    <Table.Cell>{formatStakingTime(s.time + (s.duration * 24 * 60 * 60 * 1000))}</Table.Cell>
                    <Table.Cell>{s.estimateApy}%</Table.Cell>
                    <Table.Cell>{library[s.status]}</Table.Cell>
                    {/* <Table.Cell>
                      {s.type === "FLEXIBLE" && s.status === "OPEN" && (
                        <Link to="#" onClick={() => _handleCancel(s.id)}>
                          {CANCEL}
                        </Link>
                      )}
                    </Table.Cell> */}
                  </Table.Row>
                ))
              ) : (
                <Table.Row>
                  <Table.Cell colSpan={6}>
                    <Segment basic textAlign="center">
                      {NO_RECORDS_FOUND}
                    </Segment>
                  </Table.Cell>
                </Table.Row>
              )}
            </Table.Body>
          </Table>
        </Segment>
        {stakingHistory.pageCount > 1 && (
          <Pagination
            activePage={stakingHistory.page}
            totalPages={stakingHistory.pageCount}
            onPageChange={(e, { activePage }) =>
              dispatch(_getStakingHistory(activePage))
            }
            pointing
            secondary
            inverted
            ellipsisItem={null}
            firstItem={null}
            lastItem={null}
            siblingRange={1}
            boundaryRange={0}
          />
        )}
      </Segment>
    )
  );
}

export default StakingHistory;
