import React, { useCallback, useEffect, useState } from "react";
import LazyLoad from "react-lazyload";
import { useDispatch, useSelector } from "react-redux";
import {
  Button,
  Confirm,
  Dimmer,
  Dropdown,
  Form,
  Grid,
  Header,
  Icon,
  Image,
  Input,
  Label,
  List,
  Loader,
  Message,
  Segment,
} from "semantic-ui-react";
import { SET_MODAL_CONTENT } from "../../redux/settingReducer";
import {
  _getFundList,
  _getSwapHistory,
  _getSwapList,
} from "../../redux/walletReducer";
import { S3_URL } from "../../settings";
import { deleteText, formatAmount, formatMoney } from "../../settings/format";
import { post } from "../../utils/api";
import AmountInput from "../AmountInput";
import Countdown from "../Countdown";
import { checkEmpty } from "../Validate";

function ConfirmForm({
  _handleClose,
  _handleSwap,
  quote,
  base,
  amount,
  price,
}) {
  const { setting } = useSelector((state) => state);
  const { library } = setting;
  const [time, setTime] = useState(30);

  const {
    RATIO,
    SWAP_FEE,
    SWAP_CONFIRM,
    YOU_WILL_GET,
    SWAP,
    CONFIRM,
  } = library;

  useEffect(() => {
    const interval = setInterval(() => {
      setTime(time - 1);
    }, 1000);
    if (time === 0) clearInterval(interval);
    return () => clearInterval(interval);
  }, [time]);

  return (
    <Segment
      style={{ backgroundColor: "var(--light-blue)", width: 350 }}
      // loading={loading}
      className="spwallet-container"
    >
      <Segment vertical>
        <Header style={{ color: "var(--green)" }}>{SWAP_CONFIRM}</Header>
        <Icon
          name="close"
          style={{ position: "absolute", right: 0, top: 0, cursor: "pointer" }}
          size="large"
          onClick={_handleClose}
        />
      </Segment>
      <Segment vertical>
        <div>{YOU_WILL_GET}</div>
        <Header as="h2">
          {formatAmount(parseFloat(amount * price))} {quote}
        </Header>
        <Segment style={{ backgroundColor: "var(--gray)" }}>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              marginBottom: "1em",
            }}
          >
            <div>{SWAP}: </div>
            <div>
              {formatAmount(amount)} {base}
            </div>
          </div>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              marginBottom: "1em",
            }}
          >
            <div>{RATIO}:</div>
            <div>
              1 {base} = {formatAmount(price)} {quote}
            </div>
          </div>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <div>{SWAP_FEE}:</div>
            <div>0 {base}</div>
          </div>
        </Segment>
      </Segment>
      <Form onSubmit={_handleSwap}>
        <Button className="spwallet-button" fluid disabled={!time}>
          {CONFIRM} ({time}s)
        </Button>
      </Form>
    </Segment>
  );
}

function SwapForm() {
  const { wallet, setting } = useSelector((state) => state);
  const { library } = setting;
  const { swapProduct } = wallet;
  const [base, setBase] = useState("USDT");
  const [quote, setQuote] = useState("mUSD");
  const dispatch = useDispatch();
  const [bases, setBases] = useState([]);
  const [quotes, setQuotes] = useState([]);
  const [baseAmount, setBaseAmount] = useState(0);
  const [quoteAmount, setQuoteAmount] = useState(0);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);
  const { fundList } = wallet;
  const [openConfirm, setOpenConfirm] = useState(false);
  const [data, setData] = useState(null);

  const _handleChangeBase = (value) => {
    const newBases = [];
    const newQuotes = [];
    setError(null);
    swapProduct.forEach((element, index) => {
      if (newBases.findIndex((i) => i.value === element.base) < 0) {
        newBases.push({
          key: index,
          value: element.base,
          text: (
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "space-between",
                width: 70,
              }}
            >
              <Image
                src={`${S3_URL}/coins/${element.base}.png`}
                style={{ width: 20, height: 20 }}
              />
              {element.base}
            </div>
          ),
        });
      }
      if (element.base === value) {
        newQuotes.push({
          key: index,
          value: element.quote,
          text: (
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "space-between",
                width: 70,
              }}
            >
              <Image
                src={`${S3_URL}/coins/${element.quote}.png`}
                style={{ width: 20, height: 20 }}
              />
              {element.quote}
            </div>
          ),
        });
      }
    });
    setBases(newBases);
    setQuotes(newQuotes);
    setBase(value);
    let newQuote = quote;
    if (newQuotes.findIndex((item) => item.value === quote) < 0) {
      setQuote(newQuotes[0].value);
      newQuote = newQuotes[0].value;
    }
    const selectedProduct = swapProduct.find(
      (element) => element.base === value && element.quote === newQuote
    );
    setQuoteAmount(selectedProduct.price * baseAmount);
  };

  const _handleChangeQuote = (e, { value }) => {
    const selectedProduct = swapProduct.find(
      (element) => element.base === base && element.quote === value
    );
    setQuote(value);
    setQuoteAmount(selectedProduct.price * baseAmount);
  };

  const selectedProduct = swapProduct.find(
    (element) => element.base === base && element.quote === quote
  );

  useEffect(() => {
    if (swapProduct.length === 0) {
      dispatch(_getSwapList());
    } else {
      _handleChangeBase(base);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dispatch, swapProduct]);

  const _onChangeBaseAmount = (e) => {
    let { value } = e.target;
    value = deleteText(value);
    setBaseAmount(value);
    if (value > 0) {
      setQuoteAmount(parseFloat(value) * selectedProduct.price);
    } else {
      setQuoteAmount(0);
    }
  };

  const _onChangeQuoteAmount = (e) => {
    let { value } = e.target;
    value = deleteText(value);
    setQuoteAmount(value);
    if (value) {
      setBaseAmount((parseFloat(value) / selectedProduct.price).toString());
    }
  };

  const _checkSwap = () => {
    setError(null);
    if (checkEmpty(baseAmount)) {
      setError("PLEASER_ENTER_BASE_AMOUNT");
    } else if (baseAmount < selectedProduct.minAmount) {
      setError("BASE_AMOUNT_GREATER_MIN");
    } else if (baseAmount > fund.amount) {
      setError("UNAVAILABLE_BALANCE");
    } else {
      dispatch(_getSwapList());
      setOpenConfirm(true);
    }
  };

  const fund = fundList && fundList.find((e) => e.coin === base);
  const fundQuote = fundList && fundList.find((e) => e.coin === quote);

  const { SWAP_TIP_0, SWAP_TIP_1, SWAP_TIP_2, SWAP_TIP_3 } = library;
  const tips = [SWAP_TIP_0, SWAP_TIP_1, SWAP_TIP_2, SWAP_TIP_3];

  const _handleSwap = () => {
    setLoading(true);
    setOpenConfirm(false);
    post(
      `/swap-service/swap`,
      { base, quote, price: selectedProduct.price, amount: baseAmount },
      () => {
        dispatch({
          type: SET_MODAL_CONTENT,
          payload: {
            content: "SWAP_SUCCESS",
            callback: () => {
              setLoading(false);
              setOpenConfirm(false);
            },
          },
        });
        dispatch(_getFundList());
        dispatch(_getSwapHistory());
      },
      (error) => {
        dispatch({
          type: SET_MODAL_CONTENT,
          payload: {
            content: error.code,
            callback: () => {
              setOpenConfirm(false);
            },
          },
        });
      }
    );
  };

  return swapProduct.length > 0 ? (
    <Grid stretched className="spwallet-container">
      <Grid.Column computer={8} tablet={8} only="computer">
        <Segment>
          <Segment basic>
            {selectedProduct && (
              <>
                <Header as="h5">
                  {library.RATIO}: 1 {selectedProduct.base} &asymp;{" "}
                  {formatAmount(selectedProduct.price)} {selectedProduct.quote}
                </Header>
              </>
            )}
          </Segment>
          <Segment className="spwallet-tips">
            <Segment vertical basic>
              <Icon name="lightbulb" size="large" />
              Tips:
            </Segment>
            <List.Item as="ul">
              {tips.map((item, index) => (
                <List.Item as="li" key={index}>
                  {item}
                </List.Item>
              ))}
            </List.Item>
          </Segment>
        </Segment>
      </Grid.Column>
      <Grid.Column computer={8} tablet={8} mobile={16}>
        <Segment loading={loading}>
          <div className="show-mobile">
            <Segment basic vertical>
              {selectedProduct && (
                <>
                  <Header as="h5">
                    {library.RATIO}: 1 {selectedProduct.base} &asymp;{" "}
                    {formatAmount(selectedProduct.price)}{" "}
                    {selectedProduct.quote}
                  </Header>
                </>
              )}
            </Segment>
          </div>
          {swapProduct.length > 0 && (
            <Form error inverted onSubmit={_checkSwap}>
              <Form.Field>
                <Form.Field>
                  <label
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      alignItems: "flex-end",
                    }}
                  >
                    {library.EXCHANGE}
                    <div>
                      <div>{library.MINIMUM_SWAP_AMOUNT}</div>
                      <div style={{ float: "right", marginTop: 5 }}>
                        <Label as="h5">
                          {selectedProduct && selectedProduct.minAmount} {base}
                        </Label>
                      </div>
                    </div>
                  </label>
                  <AmountInput
                    action={
                      <Dropdown
                        value={base}
                        options={bases}
                        onChange={(e, { value }) => _handleChangeBase(value)}
                        button
                        basic
                        floating
                      />
                    }
                    fluid
                    placeholder={library.PLEASE_ENTER_AMOUNT}
                    value={baseAmount}
                    onChange={_onChangeBaseAmount}
                  />
                </Form.Field>
                <Form.Group>
                  <Form.Field>
                    <Label>
                      {library.BALANCE}: {fund && formatAmount(fund.amount)}
                    </Label>
                  </Form.Field>
                  <Form.Field className="spwallet-swap_options">
                    <Button
                      onClick={_onChangeBaseAmount}
                      value={fund && fund.amount}
                      type="button"
                    >
                      MAX
                    </Button>
                  </Form.Field>
                </Form.Group>
              </Form.Field>
              <Form.Field style={{ textAlign: "center", padding: 0 }}>
                <label>&nbsp;</label>
                <Icon
                  name="exchange"
                  size="large"
                  circular
                  rotated="clockwise"
                />
              </Form.Field>
              <Form.Field>
                <label>{library.RECEIVE}</label>
                <AmountInput
                  action={
                    <Dropdown
                      value={quote}
                      options={quotes}
                      onChange={_handleChangeQuote}
                      button
                      basic
                      floating
                    />
                  }
                  placeholder={library.PLEASE_ENTER_AMOUNT}
                  value={quoteAmount}
                  onChange={_onChangeQuoteAmount}
                  fluid
                />
              </Form.Field>
              <Form.Field>
                <Label>
                  {library.BALANCE}: {fund && formatAmount(fundQuote.amount)}
                </Label>
              </Form.Field>
              <Message error content={library[error]} floating />
              {selectedProduct && (
                <Button
                  style={{ marginTop: "3em" }}
                  className="spwallet-button"
                  type="submit"
                  fluid
                >
                  {library.SWAP}
                </Button>
              )}
            </Form>
          )}
        </Segment>
      </Grid.Column>
      <Dimmer active={openConfirm} page>
        {selectedProduct && (
          <ConfirmForm
            _handleClose={() => setOpenConfirm(false)}
            _handleSwap={_handleSwap}
            price={selectedProduct.price}
            amount={baseAmount}
            base={base}
            quote={quote}
          />
        )}
      </Dimmer>
    </Grid>
  ) : null;
}

export default SwapForm;
