import React, { useState } from "react";
import LazyLoad from "react-lazyload";
import { useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import {
  Button,
  Container,
  Form,
  Grid,
  Icon,
  Image,
  Input,
  Segment,
  Statistic,
} from "semantic-ui-react";
import CoinChart from "../../components/CoinChart";
import { S3_URL } from "../../settings";
import { formatAmount, formatMoney, formatTotal } from "../../settings/format";

function Dashboard() {
  const history = useHistory();
  const { wallet, setting } = useSelector((state) => state);
  const { library } = setting;
  const { fundList } = wallet;
  let searchCoin;
  const [words, setWords] = useState("");

  let sum = fundList.reduce((a, b) => {
    return a + b.usdAmount;
  }, 0);

  if (words.trim().length > 0) {
    searchCoin = fundList.filter(
      (element) =>
        element.coin.toLowerCase().includes(words.toLowerCase()) ||
        element.fullName.toLowerCase().includes(words.toLowerCase())
    );
  } else {
    searchCoin = fundList;
  }

  return (
    <Container textAlign="center" className="dashboard">
      <LazyLoad>
        <div style={{ position: "relative" }}>
          <CoinChart />
          <Statistic
            size="small"
            inverted
            style={{
              position: "absolute",
              bottom: 0,
              top: 0,
              right: "50%",
              left: "50%",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              margin: 0,
            }}
          >
            <Statistic.Label style={{ color: "var(--green)" }}>
              {library.TOTAL}
            </Statistic.Label>
            <Statistic.Value>
              <span style={{ fontSize: 25 }}>$</span>
              <span style={{ fontSize: 35 }}>{formatTotal(sum)}</span>
            </Statistic.Value>
          </Statistic>
        </div>
      </LazyLoad>
      <LazyLoad>
        <Segment className="balances" padded>
          <Grid columns="equal" textAlign="right">
            <Grid.Row style={{ marginBottom: 0, paddingTop: "0.5em" }}>
              <Grid.Column
                computer="12"
                tablet="10"
                only="computer"
              ></Grid.Column>
              <Grid.Column
                mobile="16"
                computer="4"
                tablet="6"
                style={{ padding: 0 }}
              >
                <Form>
                  <Form.Input
                    placeholder={library.SEARCH_COIN_TOKEN}
                    type="text"
                    icon={<Icon name="search" inverted />}
                    onChange={(e, { value }) => setWords(value)}
                    fluid
                  />
                </Form>
              </Grid.Column>
            </Grid.Row>
            <Grid.Row only="computer" style={{ fontWeight: 600 }}>
              <Grid.Column textAlign="left" width={4}>
                {library.COIN_TOKEN}
              </Grid.Column>
              <Grid.Column>{library.PRICE}</Grid.Column>
              <Grid.Column>{library.BALANCE}</Grid.Column>
              <Grid.Column>{library.VALUE}</Grid.Column>
              <Grid.Column></Grid.Column>
              <Grid.Column></Grid.Column>
            </Grid.Row>
            {searchCoin.map((w, index) => (
              <React.Fragment key={index}>
                <Grid.Row only="computer">
                  <Grid.Column
                    style={{ display: "flex" }}
                    textAlign="left"
                    width={4}
                  >
                    <Image
                      src={`${S3_URL}/coins/${w.icon}`}
                      verticalAlign="middle"
                      className="coin-image"
                    />
                    <div style={{ marginLeft: 20 }}>
                      <div style={{ fontWeight: 600, fontSize: 15 }}>
                        {w.coin}
                      </div>
                      <div>{w.fullName}</div>
                    </div>
                  </Grid.Column>
                  <Grid.Column verticalAlign="middle">
                    ${formatMoney(w.usdPrice)}
                  </Grid.Column>
                  <Grid.Column
                    verticalAlign="middle"
                    style={{ color: w.color, fontWeight: 600 }}
                  >
                    {formatAmount(w.amount)}
                  </Grid.Column>
                  <Grid.Column
                    verticalAlign="middle"
                    style={{ color: w.color, fontWeight: 600 }}
                  >
                    ${formatMoney(w.usdAmount)}
                  </Grid.Column>
                  <Grid.Column textAlign="right">
                    <Button
                      onClick={() => history.push("/deposit/" + w.coin)}
                      className="spwallet-button"
                      disabled={!w.depositEnable}
                    >
                      {library.DEPOSIT}
                    </Button>
                  </Grid.Column>
                  <Grid.Column textAlign="left">
                    <Button
                      onClick={() => history.push("/withdraw/" + w.coin)}
                      basic
                      inverted
                      disabled={!w.withdrawEnable}
                    >
                      {library.WITHDRAW}
                    </Button>
                  </Grid.Column>
                </Grid.Row>
                <Grid.Row only="mobile tablet">
                  <Grid.Column
                    textAlign="left"
                    style={{ display: "flex", justifyContent: "space-between" }}
                  >
                    <Grid.Column style={{ display: "flex" }} width={8}>
                      <Image
                        src={`${S3_URL}/coins/${w.icon}`}
                        verticalAlign="middle"
                        className="coin-image"
                      />
                      <div style={{ marginLeft: 20 }}>
                        <div>{w.fullName}</div>
                        <div>${formatMoney(w.usdPrice)}</div>
                        {/* <div>{w.coin}</div> */}
                      </div>
                    </Grid.Column>
                    <Grid.Column width={8}>
                      <div
                        style={{
                          textAlign: "right",
                          color: w.color,
                          fontWeight: 600,
                        }}
                      >
                        {formatAmount(w.amount)} {w.coin}
                      </div>
                      <div
                        style={{
                          textAlign: "right",
                          color: w.color,
                          fontWeight: 600,
                        }}
                      >
                        ${formatMoney(w.usdAmount)}
                      </div>
                    </Grid.Column>
                  </Grid.Column>
                  <Grid.Column
                    width={16}
                    textAlign="center"
                    style={{ marginTop: "1em" }}
                  >
                    <Button
                      onClick={() => history.push("/deposit/" + w.coin)}
                      className="spwallet-button"
                      disabled={!w.depositEnable}
                    >
                      {library.DEPOSIT}
                    </Button>
                    <Button
                      onClick={() => history.push("/withdraw/" + w.coin)}
                      basic
                      inverted
                      disabled={!w.withdrawEnable}
                    >
                      {library.WITHDRAW}
                    </Button>
                  </Grid.Column>
                </Grid.Row>
              </React.Fragment>
            ))}
          </Grid>
        </Segment>
      </LazyLoad>
    </Container>
  );
}

export default Dashboard;
