import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Header,
  Icon,
  Pagination,
  Popup,
  Segment,
  Table,
} from "semantic-ui-react";
import { _getDepositHistory } from "../../redux/walletReducer";
import { getLinkHash } from "../../settings";
import { formatAddress, formatAmount, formatTime } from "../../settings/format";

function DepositHistory({ coin }) {
  const { wallet, setting } = useSelector((state) => state);
  const { depositHistory } = wallet;
  const { library } = setting;
  const { TIME, ADDRESS, AMOUNT, NO_RECORDS_FOUND, DEPOSIT_HISTORY } = library;

  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(_getDepositHistory(coin, 0, 0, 1, 10));
  }, [coin, dispatch]);

  return (
    depositHistory && (
      <Segment textAlign="center">
        <Header textAlign="left">{DEPOSIT_HISTORY}</Header>
        <Segment vertical basic className="spwallet-history">
          <Table
            unstackable
            basic="very"
            compact="very"
            celled
            inverted
            singleLine
            textAlign="left"
          >
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>{TIME}</Table.HeaderCell>
                <Table.HeaderCell>{ADDRESS}</Table.HeaderCell>
                <Table.HeaderCell>Hash</Table.HeaderCell>
                <Table.HeaderCell>{AMOUNT}</Table.HeaderCell>
              </Table.Row>
            </Table.Header>
            <Table.Body>
              {depositHistory.items.length > 0 ? (
                depositHistory.items.map((h, index) => (
                  <Table.Row key={index}>
                    <Table.Cell>{formatTime(h.time)}</Table.Cell>
                    <Table.Cell>
                      {h.address && (
                        <Popup
                          content="Copied"
                          on="click"
                          basic
                          position="bottom center"
                          hideOnScroll
                          trigger={
                            <p
                              style={{
                                textOverflow: "ellipsis",
                                overflow: "hidden",
                                cursor: "pointer",
                              }}
                              onClick={() =>
                                navigator.clipboard.writeText(h.address)
                              }
                            >
                              {formatAddress(h.address)}
                              <Icon
                                name="copy outline"
                                style={{ fontSize: "1em" }}
                              />
                            </p>
                          }
                        />
                      )}
                    </Table.Cell>
                    <Table.Cell>{getLinkHash(h)}</Table.Cell>
                    <Table.Cell>
                      {formatAmount(h.amount)} {h.coin}
                    </Table.Cell>
                  </Table.Row>
                ))
              ) : (
                <Table.Row>
                  <Table.Cell colSpan={3}>
                    <Segment basic textAlign="center">
                      {NO_RECORDS_FOUND}
                    </Segment>
                  </Table.Cell>
                </Table.Row>
              )}
            </Table.Body>
          </Table>
        </Segment>
        {depositHistory.pageCount > 1 && (
          <Pagination
            totalPages={depositHistory.pageCount}
            activePage={depositHistory.page}
            onPageChange={(e, { activePage }) =>
              dispatch(_getDepositHistory(coin, 0, 0, activePage, 10))
            }
            pointing
            secondary
            inverted
          />
        )}
      </Segment>
    )
  );
}

export default DepositHistory;
