import React from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import {
  Grid,
  Header,
  Label,
  Progress,
  Segment,
  Button,
} from "semantic-ui-react";
import { formatAmount, formatStakingTime } from "../../settings/format";

function CloseEasyBuyList({ list }) {
  const { setting } = useSelector((state) => state);
  const { library } = setting;
  list = list.filter((element) => !element.isActive);

  return (
    <Grid className="easy-buy-list">
      {list &&
        list.map((item, index) => (
          <Grid.Column mobile={16} computer={5} tablet={8} key={index}>
            <Link to={`/easy-buy/${item.id}`}>
              <div className="shadow">
                <Segment
                  className="information"
                  style={{
                    backgroundImage: `url(${item.coverPhoto})`,
                  }}
                >
                  <Label>
                    {item.isActive
                      ? library.OPEN.toUpperCase()
                      : library.CLOSED.toUpperCase()}
                  </Label>
                  <div className="space"></div>
                  <Header>
                    {item.name} <small>{item.base}</small>
                  </Header>
                  <span>{item.description}</span>
                  <br />
                  <small>
                    {library.BUY} {item.base} {library.BY} {item.quote}
                  </small>
                </Segment>
                <Segment className="parameter">
                  <div>
                    <p>{library.PRICE}</p>
                    <p>
                      {formatAmount(item.price)} {item.quote}
                    </p>
                  </div>
                  <div>
                    <p>{library.SESSION_SUPPLY}</p>
                    <p>
                      {formatAmount(item.supply)} {item.base}
                    </p>
                  </div>
                  <div>
                    <p>{library.SOLD}</p>
                    <p>
                      {formatAmount(item.sold)} {item.base} (
                      {parseInt((item.sold / item.supply) * 100)}%)
                    </p>
                  </div>
                  <Progress
                    percent={parseInt((item.sold / item.supply) * 100)}
                    size="tiny"
                  />
                  <div>
                    <p>{library.START_DATE}</p>
                    <p>{formatStakingTime(item.startDate)}</p>
                  </div>
                  <div>
                    <p>{library.END_DATE}</p>
                    <p>{formatStakingTime(item.endDate)}</p>
                  </div>
                </Segment>
              </div>
            </Link>
          </Grid.Column>
        ))}
    </Grid>
  );
}

export default CloseEasyBuyList;
