import { math } from "@amcharts/amcharts4/core";
import { SET_MODAL_CONTENT } from "../../redux/settingReducer";
import { _getFundList, _getWithdrawHistory } from "../../redux/walletReducer";
import { deleteText, formatAmount, formatMoney } from "../../settings/format";
import { get, post } from "../../utils/api";
import { getDeviceSecret } from "../../utils/auth";
import { checkEmpty } from "../Validate";
import QrReader from "react-qr-reader";
import AmountInput from "../AmountInput";
import { S3_URL } from "../../settings";
import {
  Dimmer,
  Form,
  Grid,
  Header,
  Icon,
  Label,
  List,
  Message,
  Popup,
  Segment,
  Button,
  Dropdown,
} from "semantic-ui-react";

const { useState, useEffect } = require("react");
const { useSelector, useDispatch } = require("react-redux");
const { useParams, useHistory } = require("react-router-dom");

function WithdrawForm() {
  const { setting, wallet, user } = useSelector((state) => state);
  const { coin } = useParams();
  const { fundList } = wallet;
  const [fund, setFund] = useState(null);
  const [address, setAddress] = useState("");
  const [amount, setAmount] = useState("");
  const [loading, setLoading] = useState(false);
  const [network, setNetwork] = useState(null);
  const [error, setError] = useState(null);
  const { library } = setting;
  const { gaEnable } = user;
  const [gaCode, setGaCode] = useState("");
  const dispatch = useDispatch();
  const [openConfirm, setOpenConfirm] = useState(false);
  const history = useHistory();
  const [showScanQrCode, setShowScanQrCode] = useState(false);
  const [addressTag, setAddressTag] = useState("");

  useEffect(() => {
    const temp = fundList.find((f) => f.coin === coin);
    if (temp) {
      setFund(temp);
      setNetwork(temp.networks[0]);
    }
  }, [coin, fund, fundList]);

  const tempAmount =
    network &&
    Math.round(parseFloat(amount) * 10 ** 8 - network.withdrawFee * 10 ** 8) /
      10 ** 8;

  const _checkWithdraw = () => {
    setError(null);
    if (checkEmpty(address)) {
      setError("PLEASE_ENTER_WALLET_ADDRESS");
    } else if (amount.trim().length === 0 || parseFloat(amount) === 0) {
      setError("PLEASE_ENTER_AMOUNT_WITHDRAW");
    } else if (tempAmount <= 0) {
      setError("WITHDRAW_AMOUNT_TOO_SMALL");
    } else if (tempAmount > fund.amount) {
      setError("UNAVAILABLE_BALANCE");
    } else if (!gaCode) {
      setError("PLEASE_ENTER_GA_CODE");
    } else if (gaCode.length !== 6) {
      setError("WRONG_GACODE");
    } else {
      setOpenConfirm(true);
    }
  };

  const _handleWithdraw = () => {
    setLoading(true);
    setOpenConfirm(false);
    console.log(tempAmount);
    get(
      `/fund-service/address/validate?address=${address}&network=${network.network}`,
      () => {
        post(
          `/fund-service/withdraw`,
          {
            coin,
            network: network.network,
            address,
            amount: tempAmount,
            deviceSecret: getDeviceSecret(),
            gaCode,
            addressTag,
          },
          () => {
            setError(null);
            setAmount("");
            setAddress("");
            setGaCode("");
            dispatch({
              type: SET_MODAL_CONTENT,
              payload: {
                content: "WITHDRAW_SUCCESSFUL",
                callback: () => {
                  dispatch(_getWithdrawHistory(coin, 0, 0));
                  dispatch(_getFundList());
                  setLoading(false);
                },
              },
            });
          },
          (error) => {
            console.log(error);
            setError(error.code);
            setLoading(false);
          }
        );
      },
      () => {
        setLoading(false);
        setError(library.INVALID_WALLET_ADDRESS);
      }
    );
  };

  const _handleChange = (e, { value }) => {
    const temp = fundList.find((f) => f.coin === coin);
    const network = temp.networks.find((f) => f.network === value);
    setNetwork(network);
  };

  const _handleChangeAmount = (e) => {
    setAmount(deleteText(e.target.value));
  };

  const {
    MINIMUM_WITHDRAW_AMOUNT,
    CHOOSE_NETWORK,
    ADDRESS,
    PLEASE_ENTER_WALLET_ADDRESS,
    AMOUNT,
    PLEASE_ENTER_AMOUNT,
    PLEASE_ENTER_GA_CODE,
    TRANSACTION_FEE,
    YOU_WILL_GET,
    WITHDRAW,
    GA_CODE,
    WITHDRAW_TIP_0,
    WITHDRAW_TIP_2,
    VALUE,
    WITHDRAW_CONFIRM,
  } = library;

  const tips = [WITHDRAW_TIP_0, WITHDRAW_TIP_2];

  const { data } = user;
  let checkWithdrawStatus = null;
  if (data) {
    if (data.verifyStatusLv2 !== "VERIFIED") {
      checkWithdrawStatus = library.USER_NOT_VERIFIED;
    } else {
      if (!gaEnable) {
        checkWithdrawStatus = library.PLEASE_ENABLE_GA;
      }
    }
  }

  const selectFund = [];
  if (fundList) {
    fundList.forEach((element, index) => {
      selectFund.push({
        key: element.coin,
        value: element.coin,
        text: element.coin + " - " + element.amount,
        image: { avatar: true, src: `${S3_URL}/coins/${element.icon}` },
      });
    });
  }

  const networkList = [];
  if (fund) {
    fund.networks.forEach((element) => {
      networkList.push({
        key: element.network,
        value: element.network,
        text: element.network,
      });
    });
  }

  return (
    fund && (
      <Grid stretched className="spwallet-container">
        <Grid.Column computer={8} tablet={8} only="computer">
          <Segment>
            {selectFund.length > 0 && (
              <Dropdown
                options={selectFund}
                selection
                fluid
                onChange={(e, { value }) => {
                  setError(null);
                  setAddress("");
                  history.push(`/withdraw/${value}`);
                }}
                value={history.location.pathname.replace("/withdraw/", "")}
              />
            )}
            <Segment vertical basic textAlign="right">
              <Label>
                {VALUE}: ~${formatMoney(fund.usdAmount)}
              </Label>
            </Segment>
            <Segment className="spwallet-tips">
              <Segment vertical basic>
                <Icon name="lightbulb" size="large" />
                Tips:
              </Segment>
              <List.Item as="ul">
                {tips.map((item, index) => (
                  <List.Item key={index} as="li">
                    {item}
                  </List.Item>
                ))}
              </List.Item>
            </Segment>
          </Segment>
        </Grid.Column>
        <Grid.Column computer={8} tablet={8} mobile={16}>
          <Segment textAlign="left" loading={loading}>
            <Segment basic vertical className="show-mobile">
              {selectFund.length > 0 && (
                <Dropdown
                  options={selectFund}
                  selection
                  fluid
                  onChange={(e, { value }) =>
                    history.push(`/withdraw/${value}`)
                  }
                  value={history.location.pathname.replace("/withdraw/", "")}
                />
              )}
              <Segment vertical basic textAlign="right">
                <Label>
                  {VALUE}: ~${formatMoney(fund.usdAmount)}
                </Label>
              </Segment>
            </Segment>
            {fund.withdrawEnable ? (
              <>
                <Form onSubmit={_checkWithdraw} inverted error>
                  <Form.Field>
                    <label>{CHOOSE_NETWORK}</label>
                    {networkList && (
                      <Dropdown
                        options={networkList}
                        selection
                        onChange={_handleChange}
                        value={network.network}
                      />
                    )}
                  </Form.Field>
                  <Form.Field>
                    <Form.Input
                      label={ADDRESS}
                      placeholder={PLEASE_ENTER_WALLET_ADDRESS}
                      onChange={(e) => setAddress(e.target.value)}
                      value={address}
                      icon={
                        <Icon
                          name="qrcode"
                          inverted
                          onClick={() => setShowScanQrCode(true)}
                          className="show-mobile"
                          link
                        />
                      }
                      maxLength="128"
                    />
                  </Form.Field>
                  {network.hasAddressTag && (
                    <Form.Input
                      label={
                        <label
                          style={{ display: "flex", alignItems: "flex-end" }}
                        >
                          MEMO
                          <Popup
                            trigger={
                              <Icon
                                name="info"
                                circular
                                style={{
                                  fontSize: "0.5em",
                                  boxShadow: "0 0 0 0.1em var(--white) inset",
                                  marginLeft: "0.5em",
                                  cursor: "help",
                                }}
                              />
                            }
                            content={
                              <p style={{ fontSize: 12 }}>
                                {library.MEMO_NOTE}
                              </p>
                            }
                            basic
                            position="right center"
                          />
                        </label>
                      }
                      placeholder={library.OPTION}
                      value={addressTag}
                      onChange={(e, { value }) => {
                        setAddressTag(value);
                      }}
                      type="text"
                      maxLength="128"
                    />
                  )}
                  <Form.Input
                    label={AMOUNT}
                    placeholder={PLEASE_ENTER_AMOUNT}
                    value={amount}
                    onChange={(e, { value }) => {
                      if (value === ".") {
                        e.target.value = "";
                      }
                      const dotIndex = value.indexOf(".");
                      if (dotIndex > 0) {
                        value.replace(".", "");
                        e.target.value =
                          value.substring(0, dotIndex + 1) +
                          value
                            .substring(dotIndex, value.length)
                            .replace(/\./g, "")
                            .substring(0, 8);
                      }
                      _handleChangeAmount(e);
                    }}
                    type="text"
                  />
                  {showScanQrCode && (
                    <div className="qr-code">
                      <div>{library.SCAN_QR_CODE}</div>
                      <div>
                        <QrReader
                          delay={300}
                          onError={(data) => console.log(data)}
                          onScan={(data) => {
                            if (data) {
                              setAddress(data);
                              setShowScanQrCode(false);
                            }
                          }}
                          style={{ width: 300, margin: "2em 0" }}
                          facingMode="environment"
                        />
                      </div>
                      <div>
                        <Button
                          type="button"
                          className="spwallet-button"
                          onClick={() => setShowScanQrCode(false)}
                        >
                          {library.CLOSE}
                        </Button>
                      </div>
                    </div>
                  )}
                  <Form.Field style={{ textAlign: "right" }}>
                    <Label
                      onClick={() =>
                        setAmount(network.minimumWithdraw + network.withdrawFee)
                      }
                      style={{ cursor: "pointer" }}
                    >
                      {MINIMUM_WITHDRAW_AMOUNT}: {network.minimumWithdraw}{" "}
                      {fund.coin}
                    </Label>
                  </Form.Field>
                  {gaEnable && (
                    <Form.Input
                      label={GA_CODE}
                      placeholder={PLEASE_ENTER_GA_CODE}
                      value={gaCode}
                      onChange={(e) => setGaCode(e.target.value)}
                      maxLength="6"
                      step="any"
                    />
                  )}
                  <Segment vertical basic textAlign="right">
                    <p>
                      {AMOUNT}:{" "}
                      {parseFloat(amount)
                        ? parseFloat(amount) > fund.amount
                          ? 0
                          : formatAmount(amount)
                        : 0}{" "}
                      {coin}
                    </p>
                    <p>
                      {TRANSACTION_FEE}: {network.withdrawFee} {coin}
                    </p>
                    <p>
                      {YOU_WILL_GET}:{" "}
                      {parseFloat(amount)
                        ? parseFloat(amount) > fund.amount
                          ? 0
                          : formatAmount(tempAmount > 0 ? tempAmount : 0)
                        : 0}{" "}
                      {coin}
                    </p>
                  </Segment>
                  <Message
                    content={library[error]}
                    hidden={error === null}
                    error
                    negative
                  />
                  <Button
                    className="spwallet-button"
                    fluid
                    disabled={checkWithdrawStatus !== null}
                  >
                    {WITHDRAW}
                  </Button>
                  {checkWithdrawStatus && (
                    <Segment basic vertical>
                      Tips: {checkWithdrawStatus}
                    </Segment>
                  )}
                </Form>
              </>
            ) : (
              <Segment basic textAlign="center">
                {library.WITHDRAW_DISABLE}
              </Segment>
            )}
            <Dimmer active={openConfirm} page>
              {/* <LazyLoad> */}
              <Segment
                style={{ backgroundColor: "var(--light-blue)", width: 350 }}
                loading={loading}
              >
                <Segment vertical>
                  <Header style={{ color: "var(--green)" }}>
                    {WITHDRAW_CONFIRM}
                  </Header>
                  <Icon
                    name="close"
                    style={{
                      position: "absolute",
                      right: 0,
                      top: 0,
                      cursor: "pointer",
                    }}
                    size="large"
                    onClick={() => setOpenConfirm(false)}
                  />
                </Segment>
                <Segment vertical>
                  <Segment style={{ backgroundColor: "var(--gray)" }}>
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                      }}
                    >
                      <p>{AMOUNT}:</p>
                      <p>
                        {formatAmount(
                          math.round(parseFloat(deleteText(amount)) * 10 ** 8) /
                            10 ** 8
                        )}{" "}
                        {coin}
                      </p>
                    </div>
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                      }}
                    >
                      <p>{library.WALLET_ADDRESS}:</p>{" "}
                      <p>
                        {address.slice(0, 5)}...
                        {address.slice(address.length - 5)}
                      </p>
                    </div>
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                      }}
                    >
                      <p>{library.WITHDRAW_FEE}:</p>
                      <p>
                        {network.withdrawFee} {coin}
                      </p>
                    </div>
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                      }}
                    >
                      <p>{YOU_WILL_GET}:</p>
                      <p>
                        {formatAmount(tempAmount)} {coin}
                      </p>
                    </div>
                  </Segment>
                </Segment>
                <Button
                  onClick={_handleWithdraw}
                  fluid
                  className="spwallet-button"
                >
                  {library.CONFIRM}
                </Button>
              </Segment>
              {/* </LazyLoad> */}
            </Dimmer>
          </Segment>
        </Grid.Column>
      </Grid>
    )
  );
}

export default WithdrawForm;
